@mixin cta--basic(
    $color: $color-purewhite,
    $background: $color-red50,
    $hover-color: $color-purewhite,
    $hover-background: $color-red70,
    $outline: $color-purewhite,
    $color-border: $color-red50,
    $color-border-hover: $color-red70,
    $corners: square
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);

    display: inline-block;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;
    transition: background-color $transition-duration--long, transform $transition-duration--long;
    border: $border-width-default $border-style-solid $color-border;

    &:after {
        opacity: 0;
    }

    @if $corners == round {
        border-radius: $measure--quarter;
    }

    &,
    &:focus,
    &:active {
        background-color: $background;
        color: $color;
        border: $border-width-default $border-style-solid $color-border;
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-background;
            color: $hover-color;
            border: $border-width-default $border-style-solid $color-border-hover;
        }
    }

}

@mixin cta--basic--subelement(
    $color: $color-purewhite,
    $background: $color-red50,
    $hover-color: $color-purewhite,
    $hover-background: $color-red70,
    $outline: $color-purewhite,
    $corners: square,
    $class: 'text'
) {
    @include anchor-reset();

    display: inline-block;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;
    transition: background-color $transition-duration--long;

    @if $corners == round {
        border-radius: $measure--quarter;
    }

    &,
    &:focus,
    &:active {
        background-color: $background;
        color: $color;
    }

    &:focus {
        .#{$class} {
            @include outline-properties($outline);
        }
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-background;
            color: $hover-color;
        }
    }
}

@mixin cta--icon($icon: '\f061') {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t1,
        $color-cta--t1,
        $color-purewhite,
        $color-nearblack,
        $color-cta--t1,
        $color-cta--t1,
        round
    );
    display: inline-flex;
    position: relative;
    transition: background-color $transition-duration--long, transform $transition-duration--long;
    &:after {
        content: $icon;
        font-family: 'Font Awesome';
        opacity: 0;
        position: absolute;
        right: $measure--small2;
        transform: translateX(-$measure--threequarters);
        transition: background-color $transition-duration--long, transform $transition-duration--long;
    }
    span {
        transition: transform $transition-duration--long;
    }
    &:focus,
    &:hover {
        @include media('hover') {
            &:after {
                opacity: 1;
                transform: translateX($measure--threequarters);
            }
            span {
                transform: translateX(-$measure--threequarters);
            }
        }
    }
}
