main > .campaign {
    @include media('>=small') {
        .pathway {
            grid-column: 2/-2;
        }
    }

    @include media('<small') {
        .pathway {
            grid-column: 1/-1;
            @include default-padding;
        }
    }

    // Generic
    .ksp-block {
        grid-column: 1/-1;
    }
}